// export default Login;


import React, { useRef } from "react";
import { Button, Form } from 'react-bootstrap';
import "../Login.scss";

function Login() {
    const password = useRef();
    const id = useRef();

    function checkID() {
        let idVal = id.current.value;
        let pwVal = password.current.value;
        let email = /^([\w-]+(?:\.[\w-]+)*)@((?:[\w-]+\.)*\w[\w-]{0,66})\.([a-z]{2,6}(?:\.[a-z]{2})?)$/;
        let reg = /^(?=.*?[A-Z])(?=.*?[a-z])(?=.*?[0-9])(?=.*?[#?!@$%^&*-]).{8,}$/;

        if (false === email.test(idVal)) {
            alert('  .');
            return false;
        } else {
            if (false === reg.test(pwVal)) {
                alert(' 8  , ///   .');
                return false;
            } else if (/(\w)\1\1\1/.test(pwVal)) {
                alert('  4    .');
                return false;
            } else if (pwVal.search(idVal) > -1) {
                alert('  .');
                return false;
            } else if (pwVal.search(/\s/) != -1) {
                alert('   .');
                return false;
            } else {
                alert(' .');
                return true;
            }
        }
    }

    return (

        <div className='login_wrap'>
            <div className='login_text'>
                <h1 style={{ textAlign: "center" }}></h1>
            </div>

            <div className='login_form'>

                <div className='form_area'>

                    <Form className="mb-5">

                        <Form.Group className="mt-3" controlId="formBasicEmail">
                            <Form.Label></Form.Label>
                            <Form.Control ref={id} type="email" placeholder="Enter email" />
                            <Form.Text className="text-muted">
                                *  .
                            </Form.Text>
                        </Form.Group>

                        <Form.Group className="mt-3" controlId="formBasicPassword">
                            <Form.Label></Form.Label>
                            <Form.Control ref={password} type="password" placeholder="Password" />
                        </Form.Group>

                        <div className="mt-4">
                            <Button variant="danger" type="submit"
                                onClick={() => { checkID() }}></Button>{' '}
                            <Button variant="outline-danger" type="button"></Button>
                        </div>

                    </Form>
                </div>
            </div>
        </div>
    )
}

export default Login;
